.main {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.main-header {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  background-color: #dcdcdc;
  height: 52px;
}
.main-content {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  position: relative;
  /* need this to position inner content */
  overflow-y: auto;
  padding-top: 10px;
}
.main-footer {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
}



