@import 'assets/css/font-awesome/css/fontawesome-all.css';
@import 'assets/css/react-data-table.css';
@import 'assets/css/react-data-table-all.min.css';
@import 'assets/css/requests.css';
@import 'assets/css/style.css';
@import 'assets/css/toastr.css';
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.td-request {
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
}
.td-request__border {
  background-color: #C7F5F5;
  border-radius: 5px;
}
.td-request-pending {
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
  color: #EEB240 ;
}
.td-request-approved {
  color: #3B9051;
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
}
.td-request-rejected,
.td-request-denied {
  color: #D73339;
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
}
.td-request-inactive {
  color: rgba(13, 46, 228, 0.4);
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
}
.td-request-none {
  background-color: white;
  overflow: inherit !important;
  margin-bottom: 0px;
  cursor: pointer;
  padding: 4px 4px 4px 11px;
}
.signin-container {
  font-family: 'Roboto Slab';
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.signin-container img {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 0;
}
.signin-container__form {
  position: absolute;
  width: 476px;
  height: 587px;
}
.signin-container__banner {
  width: 377px;
  height: 171px;
  margin: 0 auto;
  background: -webkit-gradient(linear, left top, right top, from(#00A8A9), to(#007F81));
  background: -webkit-linear-gradient(left, #00A8A9, #007F81);
  background: -o-linear-gradient(left, #00A8A9, #007F81);
  background: linear-gradient(to right, #00A8A9, #007F81);
  border-radius: 5px;
  position: absolute;
  margin-left: 50px;
  padding-top: 60px;
  -webkit-box-shadow: 0px 6px 24px -5px rgba(19, 17, 19, 0.53);
  box-shadow: 0px 6px 24px -5px rgba(19, 17, 19, 0.53);
}
.signin-container__banner a {
  font-family: Roboto Slab;
  font-size: 36px;
  font-weight: bold;
  color: #f4f4f4;
  text-align: center;
  float: right;
  text-decoration: none;
}
.signin-container__banner img {
  width: 50px;
  height: 47px;
}
.signin-container__form__login {
  background-color: white;
  margin-top: 86px;
  border-radius: 5px;
  padding: 70px 70px 20px 70px;
}
.signin-container__form__login form {
  margin-top: 60px;
}
.signin-container__form__login .form-control {
  border: none;
  border-bottom: 1.5px solid #D3D2D3;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  height: 36;
  font-size: 14px;
}
.login-icon {
  font-size: 30px;
  margin-top: -1px;
  color: #464646;
  margin-left: -16px;
  font-weight: 400;
}
.col-login {
  text-align: center;
}
.col-login__btn {
  width: 175px;
  height: 41px;
  font-size: 30px;
  font-weight: bold;
  color: #00b1b2;
  cursor: pointer;
}
.col-login__btn:hover {
  text-decoration: none;
}
.login-error {
  text-align: center;
}
.navbar-left {
  margin-left: -40px;
  margin-top: -10px;
}
.navbar-brand a {
  margin-top: -14px;
  margin-left: 7px !important;
}
.navbar-right {
  margin-right: -38px !important;
}
.navbar-inverse {
  background-color: #F4F4F4 !important;
  border-radius: 0px !important;
  border-bottom: 1px solid #E3E3E3 !important;
}



